@import url(//db.onlinewebfonts.com/c/e8c6a63541de0a4211a9358cbe74690f?family=Futura);
@import url(//db.onlinewebfonts.com/c/e8c6a63541de0a4211a9358cbe74690f?family=Futura);
* {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding-top: 5em;
  background: #1f1f1f;
  color: #fff;
  padding: 1rem 3rem;
  font-size: 2rem;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 0;
}

header .name {
  font-size: 2.4rem;
  letter-spacing: 0.1rem;
  position: relative;
  font-family: "futura";
}

a {
  text-decoration: none;
}

.header a {
  color: #fff;
}

.nav-container {
  display: flex;
  grid-column-gap: 0.3rem;
  -webkit-column-gap: 0.3rem;
          column-gap: 0.3rem;
}

.full-page {
  height: calc(100vh - 4rem);
}

.homepage {
  display: flex;
  justify-content: center;
  align-items: center;
}

.homepage-title {
  display: grid;
  grid-row-gap: 1rem;
  row-gap: 1rem;
  align-items: center;
}

h1 {
  font-family: "futura";
  font-size: 8rem;
  line-height: 7rem;
  font-weight: 400;
  margin: 0;
  color: #fff;
  letter-spacing: 0.2rem;
}

.job-title {
  color: #fff;
}

.scroll-down {
  position: absolute;
  left: 10px;
  bottom: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.scroll-down span {
  -webkit-writing-mode: vertical-rl;
      -ms-writing-mode: tb-rl;
          writing-mode: vertical-rl;
  -webkit-text-orientation: mixed;
          text-orientation: mixed;
}

.scroll-down--right {
  right: 10px;
  left: auto;
}

.title {
  width: 100%;
  padding: 2rem 5rem;
  text-align: center;
}

.homepage-img {
  grid-row: 1;
}

.portfolio-drawing {
  max-width: 420px;
  width: 70%;
  margin: auto;
  display: block;
}

@media screen and (min-width: 768px) {
  .homepage-title {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto;
    align-items: center;
  }

  .homepage-img {
    grid-column: 2;
  }

  .homepage {
    display: flex;
  }

  .portfolio-drawing {
    width: 100%;
    max-width: 500px;
  }
}

.full-page {
  height: 100vh;
}

.section-header {
  display: flex;
  justify-content: left;
  padding-top: 3rem;
}

.typewriter h2 {
  color: #229977;
  font-family: monospace;
  overflow: hidden;
  border-right: 0.15em solid orange;
  white-space: nowrap;
  margin: 0 auto;
  letter-spacing: 0.2em;
  -webkit-animation: typing 3.5s steps(25, end), blink-caret 0.5s step-end infinite;
          animation: typing 3.5s steps(25, end), blink-caret 0.5s step-end infinite;
}

@-webkit-keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

/* The typewriter cursor effect */
@-webkit-keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: #777;
  }
}
@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: #777;
  }
}

.text-centered {
  text-align: center;
}

.section-header {
  display: flex;
  justify-content: center;
}

.core-skills__text {
  line-height: 1.5;
  word-spacing: 0.5rem;
}

.section-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: left;
}

